import { getToken } from '@/utils/token'
import Vue from 'vue'
import VueRouter from 'vue-router'
// import Login from '@/views/Login/myLogin.vue'
// import Layout from '@/views/Layout/myLayout.vue'
// import Home from '@/views/Home/myHome.vue'
// import User from '@/views/User/myUser.vue'
// import Search from '@/views/Search/mySearch.vue'
// import SearchResult from '@/views/Search/searchResult.vue'
// import ArticleDetail from '@/views/ArticleDetail/myArticleDetail.vue'
// import UserEdit from '@/views/User/UserEdit.vue'
// import Chat from '@/views/Chat/myChat.vue'
// 路由懒加载：使加载的app.js文件更小，提升网页打开速度
// 方法：把组件对应js,分成若干个.js，只有当路由切换到当前页的时候，才加载对应的js文件

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/layout'
  },
  {
    path: '/login',
    component: () => import(/* webpackChunkName: "Login" */ '@/views/Login/myLogin.vue')
  },
  {
    path: '/layout',
    component: () => import(/* webpackChunkName: "Layout" */ '@/views/Layout/myLayout.vue'),
    redirect: '/layout/home', // 默认显示二级页面为home首页
    children: [
      {
        path: 'home',
        component: () => import(/* webpackChunkName: "Home" */ '@/views/Home/myHome.vue'),
        meta: {
          scrollT: 0 // 保存首页离开时滚动条的位置
        }
      },
      {
        path: 'user',
        component: () => import(/* webpackChunkName: "User" */ '@/views/User/myUser.vue')
      }
    ]
  },
  {
    path: '/search',
    component: () => import(/* webpackChunkName: "Search" */ '@/views/Search/mySearch.vue')
  },
  {
    path: '/search/:kw',
    component: () => import(/* webpackChunkName: "SearchResult" */ '@/views/Search/searchResult.vue')
  },
  {
    path: '/article_detail',
    component: () => import(/* webpackChunkName: "ArticleDetail" */ '@/views/ArticleDetail/myArticleDetail.vue')
  },
  // 编辑资料
  {
    path: '/user_editor',
    component: () => import(/* webpackChunkName: "UserEdit" */ '@/views/User/UserEdit.vue')
  },
  // 小思同学
  {
    path: '/chat',
    component: () => import(/* webpackChunkName: "Chat" */ '@/views/Chat/myChat.vue')
  }
]

const router = new VueRouter({
  routes
})
// 路由全局前置守卫 --路由跳转之前执行此函数
router.beforeEach((to, from, next) => {
  // 如果已经登录了 就不能去登录界面了
  if (getToken()?.length > 0 && to.path === '/login') {
    next('/layout/home') // 跳到首页
  } else {
    next()
  }
})

export default router
